/* Media Querys */


.tsparticles-canvas-el {
    width: fit-content;
    /* height: 100%; */
    
}
.first-parallax {
    width: 100%;
    min-height: 700px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;

}
.home-container {
    position: absolute;
    /* left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); */
}
.home-h1 {
    font-size: 2.8rem;
    text-align: center;
    color: white;
}
.home-h1-firstname {
    font-size: 5rem;
    text-align: center;
    color: white;
}
.home-h1-lastname {
    font-size: 5rem;
    text-align: center;
    color: white;
}

.about-me-container {
    /* min-height: 500px; */
    /* padding: 1em; */
    width: 100%;
    background-color:white ;
    backface-visibility: hidden;
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.about-me-h1 {
    font-size: 3em;
    text-align: center;
}
.about-me-h6 {
    padding-top: 1.5em;
    padding-bottom: 1em;
    font-size: 1.2em;
}
.about-me-p {
    font-size: 1.5em;
    padding-bottom: 10px;
}
.about-me-container img{
    width: 75%;
    padding: 1.5em;
    border-radius: 25px;
}
.about-me-text {
    width: 60%;
    padding: 1.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container-between-about-work {
    background-color: #000;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
}
.second-qoute-container {
    width: auto;
    margin: auto;
}
.second-qoute {
    text-align: center;
    font-size: 2em;
    color: white;
    margin: 5rem;
    
}
.work-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.work-text-1 {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding: 1.5em;
}
.work-h1-wrapper {
    width: 100%;
    padding-left: 1.5em;
    padding-right: 1.5em;
}
.work-h1 {
    font-size: 3em;
    text-align: center;
    padding-top: .5em;
}
.work-h3 {
    font-size: 1.5em;
    padding-top: .5em;
    padding-bottom: .5em;
    
}
.work-text-1 a {
    color:black;
    font-size: 1.5em;
    text-decoration: none;
    padding-left: 0;
    width: 100px;
}
.work-img img{
    width: 100%;
    padding-top: 1em;
    padding-bottom: 1em;
}
.work-p {
    font-size: 1.5em;
}
.work-text-2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding: 1.5em;
}
.work-h6-2 {
    font-size: 1.5em;
    padding-bottom: .5em;
    padding-top: .5em;
}
.work-text-2 a {
    color:black;
    font-size: 1.5em;
    text-decoration: none;
    padding-left: 0;
    width: 100px;
}

.work-img-2 img {
    width: 100%;
    padding-top: 1em;
    padding-bottom: 1em;
}
.work-p-2 {
    font-size: 1.5em;
}
.container-between-work-skills {
    background-color: #000;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
}
.third-qoute-container {
    width: auto;
    margin: auto;
}

.third-qoute {
    text-align: center;
    font-size: 2em;
    color: white;
    margin: 5rem;
}
.skills-container {

}
.skills-h1 {
    font-size: 3em;
    text-align: center;
    padding-top: .5em;
}
.skills-images-table {
    padding: 5em;
    justify-self: center;
}
.skills-images-table img {
    width: 10%;
    padding: .7em;
    justify-items: center;
}
.container-between-skills-contact {
    background-color: #000;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
}
.fourth-qoute-container {
    width: auto;
    margin: auto;
}
.fourth-qoute {
    text-align: center;
    font-size: 2em;
    color: white;
    margin: 5rem
}
.contact-text {
    text-align: center;
}
.contact-h1 {
    font-size: 3em;
    padding: .5em;
}
.icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    padding: 30px;
}
.linkedin-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px;
}
.linkedin-h {
    width: 320px;
}
.linkedin-icon img {
    width: 45px;
    padding: 5px;
}
.instagram-icon {
    display: flex;
    flex-direction: row;
}
.instagram-h {
    width: 320px;
}
.instagram-icon img {
    width: 45px;
    padding: 5px;
}
.github-icon {
    display: flex;
    flex-direction: row;
}
.github-icon img {
    width: 45px;
    padding: 5px;
}
.github-h {
    width: 320px;
}
.email-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contact-form {
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
} 
.contact {
    margin: 2em;
    width: 60%;
}

div.MuiFormControl-root {
    margin-bottom: 30px;
}




